import React from 'react'
import './App.scss'

import { HashRouter, Switch, Route, Redirect } from 'react-router-dom'

import { TutorialsPage } from './Page/TutorialsPage'
import { WelcomePage } from './Page/WelcomePage'
import { TutorialPage } from './Page/TutorialPage'

function App () {
  return (
    <div className='App toolkit-prototype'>
      <HashRouter basename={process.env.PUBLIC_URL}>
        <Switch>
          <Route key={'route-tutorials-page'} exact path={'/tutorials'} component={TutorialsPage} />
          <Route key={'route-welcome-page'} exact path={'/welcome'} component={WelcomePage} />
          <Route key={'route-tutorial-page'} path={'/tutorials/:tutorial'} component={TutorialPage} />
          <Redirect exact from={'/'} to={'/welcome'} />
        </Switch>
      </HashRouter>
    </div>
  )
}

export default App
