import React, { useState } from 'react'
import { Button, Tag, ClickableTile } from 'carbon-components-react'
import { Time16, ArrowRight16 } from '@carbon/icons-react'

import repoMetadata from '../repo_metadata.json'

import './ListPanelItem.scss'

const startTutorial = (
  e,
  tutorialName
) => {
  e.preventDefault()
  e.stopPropagation()

  let result = null
  let tut = null
  let tutUrl = null

  if (tutorialName && repoMetadata) {
    console.log(tutorialName)
    const repoMetadataTutorial = repoMetadata.categories[0].tutorials.find(({ name }) => name === tutorialName)

    console.log('-------repoMetadataTutorial---------')
    console.log(repoMetadataTutorial)
    const selectedTutorialUrl = repoMetadataTutorial.stepsURL
    const selectedTutorialUrlSplit = selectedTutorialUrl.split('/')
    const tutorialRepoName = selectedTutorialUrlSplit[9]

    console.log('-------tutorialRepoName---------')
    console.log(tutorialRepoName)

    /*eslint-disable no-undef*/
    try {
      result = javaSetSelectedTutorialFunction(tutorialName)
    } catch (e) {
      alert('a java error in javaSetSelectedTutorialFunction occurred: ' + e.message)
    }

    try {
      result = javaOpenTutorialStepsFunction(`https://ot4i.github.io/ace-tutorials/v12/en/TutorialManager.html#/./tutorials/${tutorialRepoName}`)
    } catch (e) {
      alert('a java error in javaOpenTutorialStepsFunction occurred: ' + e.message)
    }
    /*eslint-enable no-undef*/
  }
}

export const ListPanelItem = ({
  name,
  desc,
  tags,
  durationMins,
  handleLinkClick,
  handleDetailsActive,
  handleDetailsClick,
  hasButtons
}) => {
  const baseClass = 'list-panel-item'
  const [active, setActive] = useState({
    focus: false,
    pointer: false
  })

  const [itemClasses, setItemClasses] = useState(baseClass)
  const handleClick = (preview) => {
    handleLinkClick(preview === true)
  }

  const updateItemClasses = (newActive) => {
    setItemClasses(
      newActive.focus || newActive.pointer
        ? `${baseClass} ${baseClass}--active`
        : `${baseClass}`
    )
  }

  const handleActivate = (e) => {
    const newState = { ...active }
    const type = e.type === 'focus' ? e.type : e.type.substr(0, e.type.length - 5)
    newState[type] = true
    setActive(newState)
    updateItemClasses(newState)
  }

  const handleDeactivate = (e) => {
    const newState = { ...active }
    const type = e.type === 'blur' ? 'focus' : e.type.substr(0, e.type.length - 5)
    newState[type] = false
    setActive(newState)
    updateItemClasses(newState)
  }

  return (
    <li
      className={itemClasses}
      onFocus={handleActivate}
      onBlur={handleDeactivate}
      onPointerEnter={handleActivate}
      onPointerLeave={handleDeactivate}
    >
      <ClickableTile light handleClick={handleClick} tabIndex='0'>
        <div className='list-panel-item__heading'>
          <h2 className='list-panel-item__title'>{name}</h2>
        </div>
        <p className='list-panel-item__text text--long'>
          {desc}
        </p>
        <div className='list-panel-item__lower'>
          <div className='list-panel-item__lower-left'>
            {
              durationMins && (
                <React.Fragment>
                  <Time16
                    className='list-panel-item__duration-img'
                    aria-label='Timing indicator'
                  />
                  <span className='list-panel-item__duration-text'>
                    {durationMins} minutes
                  </span>
                </React.Fragment>
              )
            }
            {
              tags && tags.map((tag) =>
                <Tag key={tag}>{tag}</Tag>
              )
            }
          </div>
          {
            hasButtons && (
              <Button
                kind='ghost'
                iconDescription='Start tutorial'
                renderIcon={ArrowRight16}
                size='sm'
                onClick={(e) => startTutorial(e, name)}
                className='list-panel-item__start-button'
              >
                Start
              </Button>
            )
          }
        </div>
      </ClickableTile>
    </li>
  )
}
