
/*
This javascript file is used by Page/TutorialsPage.js
This file has the java methods for the Tutorials page

The following callback funtions are registered by a toolkit plugin : 

/com.ibm.etools.mft.tutorials/src/com/ibm/etools/mft/tutorials/views/TutorialManager.java
/com.ibm.etools.mft.tutorials/src/com/ibm/etools/mft/tutorials/views/TutorialStepsView.java

Callback functions:
javaSetSelectedTutorialFunction 
javaOpenTutorialStepsFunction

*/
import React, { useEffect, useState } from 'react'
import { Accordion, Button} from 'carbon-components-react'
import { TutorialPreview } from './TutorialPreview'
import repoMetadata from '../repo_metadata.json'

import './Tutorial.scss'

const setSelectedTutorial = (selectName) => {
  let result = null

  console.log(selectName)

  /*eslint-disable no-undef*/
  try {
    result = javaSetSelectedTutorialFunction(selectName)
  } catch (e) {
    alert('a java error in javaSetSelectedTutorialFunction occurred: ' + e.message)
  }
  /*eslint-enable no-undef*/
}

const getTutorialData = (
  repoMetadataTutorial,
  handleLinkClick
) => {
  const stepsUrl = repoMetadataTutorial.stepsURL
  const tutorialStepsUrl = stepsUrl && stepsUrl.split('/')
  const tutorialName = tutorialStepsUrl[9]

  handleLinkClick(tutorialName)
}

const startTutorial = (
  tutorialName,
  handleLinkClick
) => {
  let result = null
  let tut = null
  let tutUrl = null

  if (tutorialName && repoMetadata) {
    const repoMetadataTutorial = repoMetadata.categories[0].tutorials.find(({ name }) => name === tutorialName)
    getTutorialData(repoMetadataTutorial, handleLinkClick)

    const selectedTutorialUrl = repoMetadataTutorial.stepsURL
    const selectedTutorialUrlSplit = selectedTutorialUrl.split('/')
    const tutorialRepoName = selectedTutorialUrlSplit[9]

    /*eslint-disable no-undef*/
    try {
      result = javaOpenTutorialStepsFunction(`https://ot4i.github.io/ace-tutorials/v12/en/TutorialManager.html#/./tutorials/${tutorialRepoName}`)
    } catch (e) {
      alert('a java error in javaOpenTutorialStepsFunction occurred: ' + e.message)
    }
    /*eslint-enable no-undef*/
  }
}

const PageSteps = ({
  preview,
  initiallyOpen,
  handleLinkClick,
  name,
  tutorialZipUrl
}) => {
  const result = []
  const [openSection, setOpenSection] = useState(
    preview ? 'Overview' : initiallyOpen ? initiallyOpen : 'Create'
  )

  const handleHeadingClick = (name) => {
    setOpenSection(name)
  }

  const [downloadCount, setDownloadCount] = useState(null)

  const tutorialZipUrlArray = tutorialZipUrl && tutorialZipUrl.split('/')
  const zipUrlTutorialName = tutorialZipUrlArray && tutorialZipUrlArray[4]
  const zipUrlTutorialVersion = tutorialZipUrlArray && tutorialZipUrlArray[7]
  const zipUrlTutorialFile = tutorialZipUrlArray && tutorialZipUrlArray[8]

  let downloadCountUrl = ''
  if (zipUrlTutorialName && zipUrlTutorialVersion && zipUrlTutorialFile) {
    downloadCountUrl = 'https://api.github.com/repos/ot4i/' + zipUrlTutorialName + '/releases/tags/' + zipUrlTutorialVersion
  }

  useEffect(() => {
    const fetchDownloadCount = async () => {
      if (downloadCountUrl && (downloadCountUrl !== '')) {
        const response = await fetch(downloadCountUrl, {
          headers: new Headers({
            Accept: 'application/vnd.github.v3+json'
          }),
          method: 'GET',
          mode: 'cors',
          credentials: 'omit' // Useful for including session ID (and, IIRC, authorization headers)
        })

        const responseData = await response.json()
        if (responseData && responseData.assets[0] && responseData.assets[0].download_count) {
          setDownloadCount(responseData.assets[0].download_count)
        }
      } else {
        setDownloadCount(null)
      }
    }
    fetchDownloadCount()
  },[downloadCountUrl])

  // Preview is always TRUE for now as this only covers Overview
  if (preview) {
    result.push(
      <li
        className='bx--accordion__item bx--accordion__item--active tutorial-stage tutorial-stage--preview'
        key='Overview-preview'
      >
        <div className='bx--accordion__title tutorial-stage--preview__title'>
          Overview
        </div>
        <div id='accordion-item-1' className='bx--accordion__content'>
          <TutorialPreview
            name={name}
            downloadCount={downloadCount}
          />
        </div>
      </li>
    )

    result.push(
      <li className='bx--accordion__item tutorial-stage' key='tutorial-start'>
        <Button
          className='tutorial-start'
          onClick={() => startTutorial(name, handleLinkClick)}
        >
          Start
        </Button>
      </li>
    )
  }

  return result
}

export const Tutorial = ({ name, preview, initiallyOpen, handleLinkClick }) => {
  const tutorialBaseClass = 'tutorial'
  const [tutorialClasses, setTutorialClasses] = useState('');
  const [tutorialName, setTutorialName] = useState('');

  useEffect(() => {
    if (preview) {
      setTutorialClasses(`${tutorialBaseClass} ${tutorialBaseClass}--preview`);
    } else {
      setTutorialClasses(`${tutorialBaseClass}`);
    }
    if (name && (name !== '')) {
      setTutorialName(name)
    } else {
      setTutorialName('')
    }

    setSelectedTutorial(name || '')
  },[preview, name])

  const repoMetadataTutorial = repoMetadata.categories[0].tutorials.find(({ name }) => name === tutorialName)
  const tutorialZipUrl = repoMetadataTutorial && repoMetadataTutorial.zipURL ? repoMetadataTutorial.zipURL : null

  return (
    <div className={tutorialClasses}>
      <main className='tutorial__content'>
        <Accordion>
          <PageSteps
            preview={preview}
            initiallyOpen={initiallyOpen}
            name={name}
            handleLinkClick={handleLinkClick}
            tutorialZipUrl={tutorialZipUrl}
          />
        </Accordion>
      </main>
    </div>
  );
};
