import React from 'react'
import { useHistory } from 'react-router-dom'

import { IdeHome } from '@hybrid-cloud/ide-components'
import './WelcomePage.scss'
import product_graphic from '../assets/message-flow-v2.svg'
import { Education32, AddComment32, Language32 } from '@carbon/icons-react'
import { LayoutNav } from '../layouts/LayoutNav'

export const WelcomePage = (route) => {
  const history = useHistory()
  const homeConfig = {
    headerOne: 'Welcome to IBM App Connect Enterprise Toolkit',
    headerTwo: 'Integrate your business. Connect your world.',
    headerGraphic: {
      imgSrc: product_graphic,
      imgAlt: 'My image alt text',
      imgClassName: 'ide--home-storybook-header'
    },
    sets: [
      {
        id: 'getting_started',
        setHeading: 'Getting Started',
        tasks: [
          {
            taskId: 'tutorials',
            cardSize: 'standard',
            taskHeading: 'Tutorials',
            taskHeadingDescription: 'Get started quickly using our tutorials',
            taskGraphic: Education32,
            onInteract: (e) => handleClick(e)
          },
          {
            taskId: 'whats-new',
            cardSize: 'standard',
            taskHeading: "What's new?",
            taskHeadingDescription: "See what's new in IBM App Connect Enterprise v12.0",
            taskGraphic: AddComment32,
            cardHref: 'https://org.eclipse.ui.intro/showHelpTopic?id=/com.ibm.etools.mft.doc/bb23850_.html'
          },
          {
            taskId: 'language-pack',
            cardSize: 'standard',
            taskHeading: 'Language pack',
            taskHeadingDescription: 'Install a toolkit language pack',
            taskGraphic: Language32,
            cardHref: 'https://org.eclipse.ui.intro/showHelpTopic?id=/com.ibm.iib.langpacks.doc/bh25997_.htm'
          }
        ]
      }
    ]
  }

  const handleClick = (e) => {
    history.push(`/${e}${route.location.search}`)
  }

  return (
    <LayoutNav>
      <IdeHome {...homeConfig} />
    </LayoutNav>
  )
}
