/*

This is a javascript file that is used for a specific Tutorial

The following callback funtions are registered by a toolkit plugin : 

/com.ibm.etools.mft.tutorials/src/com/ibm/etools/mft/tutorials/views/TutorialManager.java
/com.ibm.etools.mft.tutorials/src/com/ibm/etools/mft/tutorials/views/TutorialStepsView.java

Callback functions:
javaOpenURLInExternalBrowserFunction 
javaImportPIFileFunction 
javaGetSelectedTutorialFunction 
javaUnDeployResourcesFunction
javaDeleteProjectsFromWorkspaceFunction
javaOpenResourceEditorTutorialFunction

*/

import React, { useEffect, useState } from 'react'

import {
  Button,
  Link,
  Accordion,
  AccordionItem,
  OrderedList,
  UnorderedList,
  ListItem
} from 'carbon-components-react'

import { Time16, Download16, TrashCan16 } from '@carbon/icons-react'

import parse from 'html-react-parser'

import repoMetadata from '../repo_metadata.json'

import './TutorialPage.scss'

const TopicsListItems = ({ topics }) => {
  const result = []
  for (const topic of topics) {
    result.push(
      <ListItem className='tutorial-preview__' key={topic.title}>
        {topic.title}
      </ListItem>
    )
  }

  return result
}

const OutcomesListItems = ({ outcomes }) => {
  const result = []

  for (const outcome of outcomes) {
    result.push(
      <ListItem className='tutorial-preview__outcome' key={outcome.title}>
        {outcome.title}
      </ListItem>
    )
  }

  return result
}

export const TutorialPage = (route) => {
  const [tutorialData, setTutorialData] = useState(null)
  const [openAccordionIndex, setOpenAccordionIndex] = useState(1)

  const repoUrl = 'https://raw.githubusercontent.com/ot4i/'
  const fileUrl = '/master/v12/doc/_data.json'
  const tutorialName = route.match.params.tutorial

  useEffect(() => {
    const fetchJsonFile = async () => {
      const response = await fetch(repoUrl + tutorialName + fileUrl, {
        headers: new Headers({
          Accept: 'application/vnd.github.v3+json'
        }),
        method: 'GET',
        mode: 'cors',
        credentials: 'omit' // Useful for including session ID (and, IIRC, authorization headers)
      })
      const responseData = await response.json()

      if (responseData) {
        setTutorialData(responseData)
      }
    }

    fetchJsonFile()
  },[tutorialName])

  const pageSteps = tutorialData?.tutorial?.pageSteps
  const pageTitle = tutorialData?.tutorial?._common?.name
  const pageSubTitle = tutorialData?.tutorial?._common?.shortDescription
  const pageDetails = tutorialData?.tutorial?.pageDetails
  const createStep = pageSteps?.create
  const prepareStep = pageSteps?.prepare
  const runStep = pageSteps?.run

  const repoMetadataTutorial = repoMetadata.categories[0].tutorials.find(({ name }) => name === pageTitle)

  const helpLinkUrl = tutorialData?.tutorial?._common?.helpLink?.link
  const helpLinkText = tutorialData?.tutorial?._common?.helpLink?.text
  const onHelpLinkClick = (e) => {
    e.preventDefault()

    /*eslint-disable no-undef*/
    let result = null
    try {
      result = javaOpenURLInExternalBrowserFunction(helpLinkUrl)
    } catch (e) {
      alert('a java error in javaOpenURLInExternalBrowserFunction occurred: ' + e.message)
    }
    /*eslint-enable no-undef*/
  }

  const onLinkClick = (evt, url) => {
    evt.preventDefault()

    /*eslint-disable no-undef*/
    let result = null
    try {
      result = javaOpenURLInExternalBrowserFunction(url)
    } catch (e) {
      alert('a java error in javaOpenURLInExternalBrowserFunction occurred: ' + e.message)
    }
    /*eslint-enable no-undef*/
  }

  const onImportArtifactClick = (evt) => {
    evt.preventDefault()

    const tutorialName = pageTitle
    const repoMetadataTutorial = repoMetadata.categories[0].tutorials.find(({ name }) => name === tutorialName)
    const zipUrl = repoMetadataTutorial && repoMetadataTutorial.zipURL

    /*eslint-disable no-undef*/
    let result = null
    try {
      result = javaImportPIFileFunction(tutorialName, zipUrl)
    } catch (e) {
      alert('a java error in javaOpenURLInExternalBrowserFunction occurred: ' + e.message)
    }

    if (repoMetadataTutorial && repoMetadataTutorial.editorResPaths && repoMetadataTutorial.editorResPaths.length) {
      const resourceId = repoMetadataTutorial.editorResPaths[0].id
      resourceId && onOpenResource(resourceId)
    }
    /*eslint-enable no-undef*/
  }

  const onBacktoGalleryClick = (evt) => {
    evt.preventDefault()

    let result = null

    /*eslint-disable no-undef*/
    try {
      result = javaBackToGalleryFunction()
    } catch (e) {
      alert('a java error in javaBackToGalleryFunction occurred: ' + e.message)
    }
    /*eslint-enable no-undef*/
  }

  const onCleanUpClick = (evt) => {
    evt.preventDefault()

    let result = null
    let tut = null

    /*eslint-disable no-undef*/
    try {
      result = javaGetSelectedTutorialFunction()
      let tutInfoString = result[0]
      tut = JSON.parse(tutInfoString)
    } catch (e) {
      alert('a java error in javaGetSelectedTutorialFunction occurred: ' + e.message)
    }

    if (tut !== undefined) {
      try {
        result = javaUnDeployResourcesFunction()
      } catch (e) {
        alert('a java error in javaUnDeployResourcesFunction occurred: ' + e.message)
      }

      try {
        result = javaDeleteProjectsFromWorkspaceFunction(tut.projects.split(':'))
      } catch (e) {
        alert('a java error in javaDeleteProjectsFromWorkspaceFunction occurred: ' + e.message)
      }
    }
    /*eslint-enable no-undef*/
  }

  const onAccordionClick = (accordionObject, index) => {
    console.log(accordionObject)
    setOpenAccordionIndex(index)
  }

  const onOpenResource = (id) => {
    /*eslint-disable no-undef*/
    let result = null

    try {
      result = javaOpenResourceEditorTutorialFunction(id)
    } catch (e) {
      alert('a java error in javaOpenResourceEditorTutorialFunction occurred: ' + e.message)
    }
    /*eslint-enable no-undef*/
  }

  const details = tutorialData?.tutorial?.pageDetails || ''
  const common = tutorialData?.tutorial?._common || ''
  const summary = tutorialData?.tutorial?.pageDetails?.summary || {}

  if (tutorialData) {

    return (
      <div className="tutorial-page__tutorial">
        <div className='tutorials-page__tutorial--title-container'>
          <h3 className='tutorials-page__tutorial--title'>
            {pageTitle}
          </h3>
          <Link
            href='#'
            onClick={(e) => onBacktoGalleryClick(e)}
            className='tutorials-page__tutorial--title-link'
          >
            Back to gallery
          </Link>
        </div>
        <hr className='tutorials-page__tutorial--hr' />
        <div className='tutorials-page__tutorial--content-container'>
          <div className='tutorials-page__tutorial--subtitle-container'>
            <p className='tutorials-page__tutorial--subtitle'>
              {pageSubTitle}
            </p>
            <Link
              href='#'
              onClick={(e) => onHelpLinkClick(e)}
              className='tutorials-page__tutorial--subtitle-link'
            >
              {helpLinkText}
            </Link>
          </div>
          <br />
          <Accordion size='xl'>

            {/*
              Overview Step
              open={openAccordionIndex === 0}
              onHeadingClick={(obj) => onAccordionClick(obj, 0)}
            */}
            <AccordionItem
              title='Overview'
              className='tutorial-stage tutorial-stage--overview'
              key='tutorial-stage-overview'
            >
              <div className="tutorial-preview">
                <div className="tutorial-preview__section">
                  <span />
                  <h6>{common.shortDescription}</h6>
                </div>
                <div className="tutorial-preview__section tutorial-preview__section--row">
                  <div className="tutorial-preview__schedule" title='Estimated tutorial duration time'>
                    <Time16 className="tutorial-preview__icon" />
                    <p>{common.durationTime}.</p>
                  </div>
                </div>
                <div className="tutorial-preview__section">
                  <p className="tutorial-preview__text">{details.topicsName}</p>
                  <UnorderedList className="tutorial-preview__list">
                    <TopicsListItems topics={details.topicList} />
                  </UnorderedList>
                </div>
                <div className="tutorial-preview__section">
                  <p className="tutorial-preview__text">{details.outcomesName}</p>
                  <UnorderedList className="tutorial-preview__list">
                    <OutcomesListItems outcomes={details.outcomeList} />
                  </UnorderedList>
                </div>
                <div className="tutorial-preview__section">
                  <p className="tutorial-preview__text-header">
                    {
                      (summary && summary.overviewName) === 'OVERVIEW' ?
                      'Details' : summary.overviewName
                    }
                  </p>
                  {summary && summary.sections && summary.sections.map((item, index) => (
                    <p key={`item-section:${index}`} className="tutorial-preview__text">
                      {item.section}
                    </p>
                  ))}
                </div>
              </div>
            </AccordionItem>

            {/*
              Create Step
              open={openAccordionIndex === 1}
              onHeadingClick={(obj) => onAccordionClick(obj, 1)}
            */}
            <AccordionItem
              title='Create'
              className='tutorial-stage tutorial-stage--create'
              key='tutorial-stage-create'
              open
            >
            <div className="tutorial-preview">
              <div className="tutorial-preview__section tutorial-preview__section-inline">
                <h6 className='tutorial-preview__section-header'>{createStep.overview.heading}</h6>
                {
                  createStep && createStep.actions && createStep.actions.details && createStep.actions.details.map((detail, detailIndex) => (
                    detail.method === 'importArtifacts()' && (
                      <Button
                        href='#'
                        onClick={(e) => onImportArtifactClick(e)}
                        size='sm'
                        key={`createStep-button:${detailIndex}`}
                        renderIcon={Download16}
                        className='tutorial-preview__section-button'
                      >
                        {detail.name}
                      </Button>
                    )
                  ))
                }
              </div>
              <div className="tutorial-preview__section">
                {createStep && createStep.overview && createStep.overview.sections && createStep.overview.sections.map((section, sectionIndex) => (
                  <div key={`item-container:${sectionIndex}`}>
                    <p key={`item-section:${sectionIndex}`} className="tutorial-preview__text">
                      {section && section.section && parse(section.section)}
                    </p>
                    {
                      section.steps && section.steps.details && section.steps.details.map((detail, detailIndex) => (
                        <p key={`item-section:${detailIndex}`} className="tutorial-preview__text">
                          {detail && detail.details && parse(detail.details)}
                        </p>
                      ))
                    }
                </div>
                ))}
              </div>
              <br />
              <div className="tutorial-preview__section">
                <h6 className='tutorial-preview__section-header'>{createStep?.helpLinks?.title}</h6>
                {createStep && createStep.helpLinks && createStep.helpLinks.details && createStep.helpLinks.details.map((detail, detailIndex) => (
                  <div key={`item-container-create-help:${detailIndex}`}>
                    <Link
                      key={`item-section-create-help:${detailIndex}`}
                      href='#'
                      onClick={(e) => onLinkClick(e, detail.link)}
                    >
                      {detail.description}
                    </Link>
                </div>
                ))}
              </div>
            </div>
            </AccordionItem>

            {/*
              Prepare Step
              open={openAccordionIndex === 2}
              onHeadingClick={(obj) => onAccordionClick(obj, 2)}
            */}
            <AccordionItem
              title='Prepare'
              className='tutorial-stage tutorial-stage--prepare'
              key='tutorial-stage-prepare'
            >
            <div className="tutorial-preview">
              <div className="tutorial-preview__section tutorial-preview__section-inline">
                <h6 className='tutorial-preview__section-header'>{prepareStep.overview.heading}</h6>
              </div>
              <div className="tutorial-preview__section">
                {prepareStep && prepareStep.overview && prepareStep.overview.sections && prepareStep.overview.sections.map((section, sectionIndex) => (
                  <div key={`item-container-prepare:${sectionIndex}`}>
                    <p key={`item-section-prepare:${sectionIndex}`} className="tutorial-preview__text">
                      {section && section.section && parse(section.section)}
                    </p>
                    <OrderedList className='tutorial-preview__section--ordered-list'>
                      {
                        section.steps && section.steps.details && section.steps.details.map((detail, detailIndex) => (
                          <ListItem
                            key={`item-section-prepare:${detailIndex}`}
                            className='tutorial-preview__section--list'
                          >
                            {detail && detail.details && parse(detail.details)}
                            {detail && detail.substeps && console.log(detail.substeps.details) }                       
                            {
                              detail && detail.substeps && detail.substeps.details.map((substep, substepIndex) => (
                                <ListItem
                                  key={`item-section-prepare:${substepIndex}`}
                                  className='tutorial-preview__section--list tutorial-preview__section--list-substep'
                                >
                                {substep.details && parse(substep.details)}
                                </ListItem>
                              ))
                            }
                          </ListItem>
                        ))
                      }
                    </OrderedList>
                </div>
                ))}
              </div>
              <br />
              <div className="tutorial-preview__section">
                <h6 className='tutorial-preview__section-header'>{prepareStep?.helpLinks?.title}</h6>
                {prepareStep && prepareStep.helpLinks && prepareStep.helpLinks.details && prepareStep.helpLinks.details.map((detail, detailIndex) => (
                  <div key={`item-container-prepare-help:${detailIndex}`}>
                    <Link
                      key={`item-section-prepare-help:${detailIndex}`}
                      href='#'
                      onClick={(e) => onLinkClick(e, detail.link)}
                    >
                      {detail.description}
                    </Link>
                </div>
                ))}
              </div>
            </div>
            </AccordionItem>

            {/*
              Run Step
              open={openAccordionIndex === 3}
              onHeadingClick={(obj) => onAccordionClick(obj, 3)}
            */}
            <AccordionItem
              title='Run'
              className='tutorial-stage tutorial-stage--run'
              key='tutorial-stage-run'
            >
              <div className="tutorial-preview">
                <div className="tutorial-preview__section tutorial-preview__section-inline">
                  <h6 className='tutorial-preview__section-header'>{runStep.overview.heading}</h6>
                </div>
                <div className="tutorial-preview__section">
                  {runStep && runStep.overview && runStep.overview.sections && runStep.overview.sections.map((section, sectionIndex) => (
                    <div key={`item-container-run:${sectionIndex}`}>
                      <p key={`item-section-run:${sectionIndex}`} className="tutorial-preview__text">
                        {section && section.section && parse(section.section)}
                      </p>
                      <OrderedList className='tutorial-preview__section--ordered-list'>
                        {
                          section.steps && section.steps.details && section.steps.details.map((detail, detailIndex) => (
                            <ListItem
                              key={`item-section-run-detail:${detailIndex}`}
                              className='tutorial-preview__section--list'
                            >
                              {detail && detail.details && parse(detail.details)}
                              {detail && detail.substeps && console.log(detail.substeps.details)}
                              {
                                detail && detail.substeps && detail.substeps.details.map((substep, substepIndex) => (
                                  <ListItem
                                    key={`item-section-run:${substepIndex}`}
                                    className='tutorial-preview__section--list tutorial-preview__section--list-substep'
                                  >
                                    {substep.details && parse(substep.details)}
                                  </ListItem>
                                ))
                              }
                            </ListItem>
                          ))
                        }
                      </OrderedList>
                  </div>
                  ))}
                </div>
                <br />
                <div className="tutorial-preview__section">
                  <h6 className='tutorial-preview__section-header'>{runStep?.helpLinks?.title}</h6>
                  {runStep && runStep.helpLinks && runStep.helpLinks.details && runStep.helpLinks.details.map((detail, detailIndex) => (
                    <div key={`item-container-run-help:${detailIndex}`}>
                      <Link
                        key={`item-section-run-help:${detailIndex}`}
                        href='#'
                        onClick={(e) => onLinkClick(e, detail.link)}
                      >
                        {detail.description}
                      </Link>
                  </div>
                  ))}
                </div>
                {
                  runStep && runStep.exits && runStep.exits.details && runStep.exits.details.length && (
                    <div className="tutorial-preview__section">
                      {
                        runStep && runStep.exits && runStep.exits.details && runStep.exits.details.map((detail, detailIndex) => (
                          <div key={`item-container-run-exit:${detailIndex}`}>
                            <h6 className='tutorial-preview__section-header'>
                              {detail.cleanupDescription || detail.description}
                            </h6>
                            {
                              detail.method === 'cleanUp()' && (
                                <Button
                                  kind='danger--tertiary'
                                  key={`item-section-run-exit:${detailIndex}`}
                                  onClick={(e) => onCleanUpClick(e)}
                                  renderIcon={TrashCan16}
                                  iconDescription={detail.cleanupDescription || detail.description}
                                  size='sm'
                                  className='tutorial-preview__section-button'
                                >
                                  {detail.name}
                                </Button>
                              )
                            }
                          </div>
                        ))
                      }
                    </div>
                  )
                }
              </div>
            </AccordionItem>
          </Accordion>
        </div>
      </div>
    )
  } else {
    return ''
  }
}
